/* ------------------------------------------------------
Site Header section
--------------------------------------------------------- */
.single-product #site-header {
	border-bottom: 0;
}

#site-header {
	border-bottom: 1px solid rgba(0, 0, 0, 0.12);
	float: none;
}

/* Reset floats */
#site-header * {
	float: none;
}

#site-header .navigation-header {
	display: none;
}

#header .container {
	display: flex;
	justify-content: space-between;
	padding-top: 30px;
}

#header .container::before,
#header .container::after {
	display: none;
}

#header .container,
.navigation-header .container {
	border-bottom: 0;
}

/* ------------------------------------------------------
Pimary Site Navigation
--------------------------------------------------------- */
#primary-navigation .navigation .menu {
	display: flex;
}

/* ------------------------------------------------------
Woocommerce Breadcrumbs
--------------------------------------------------------- */
.custom-breadcrumb {
	background-color: #f2f2f2;
	padding-top: 15px;
	padding-bottom: 15px;
}

.woocommerce .woocommerce-breadcrumb {
	font-size: 0.75em !important;
	margin-bottom: 0 !important;
}

/* ------------------------------------------------------
Woocommerce styles
--------------------------------------------------------- */
.woocommerce-page #page .article {
	width: 100%;
}

.woocommerce .quantity .qty {
	border-radius: 0 !important;
}

.woocommerce a.button, .woocommerce-page a.button, .woocommerce button.button, .woocommerce-page button.button, .woocommerce input.button, .woocommerce-page input.button, .woocommerce #respond input#submit, .woocommerce-page #respond input#submit, .woocommerce #content input.button, .woocommerce-page #content input.button {
	border-radius: 0 !important;
}

/* ------------------------------------------------------
Homepage Page Template Styles
--------------------------------------------------------- */
.page-template-page-home #page {
	padding-top: 40px;
}

.page-template-page-home article header {
	float: none;
}

.page-template-page-home article header .entry-title {
	font-size: 0;
	color: transparent;
	margin: 0;
}

/* ------------------------------------------------------
Homepage Grid
--------------------------------------------------------- */
.home-grid {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-auto-rows: minmax(149px, auto);
	gap: 20px;
	margin-bottom: 60px;
}

.grid-item {
	background-color: #f0f1f2;
}

.grid-item figure {
	width: 100%;
	height: 100%;
	margin: 0;
	position: relative;
	overflow: hidden;
}

.grid-item figure img {
	width: 100%;
	height: auto;
	object-fit: cover;
	position: absolute;
	top: 0;
	left: 0;
}

.grid-item figcaption {
	background-color: #f0f1f2;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	text-align: center;
	z-index: 1;
}

.grid-item figcaption span {
	display: block;
}

.item-1 {
	grid-column: 1 / 3;
	grid-row: 1 / 4;
}

.item-2 {
	grid-column: 3 / 5;
	grid-row: 1 / 3;
}

.item-3 {
	grid-column: 3 / 4;
	grid-row: 3 / 6;
}

.item-4 {
	grid-column: 4 / 5;
	grid-row: 3 / 6;
}

.item-5 {
	grid-column: 1 / 3;
	grid-row: 4 / 6;
}

.item-6 {
	grid-column: 1 / 3;
	grid-row: 6 / 8;
}

.item-7 {
	grid-column: 3 / 4;
	grid-row: 6 / 8;
}

.item-8 {
	grid-column: 4 / 5;
	grid-row: 6 / 8;
}

/* ------------------------------------------------------
Site Footer section
--------------------------------------------------------- */
#site-footer {
	background-color: #f0f1f2 !important;
	border-top: 0;
	background-color: blue;
}
